<template>
  <div>
    <el-card class="">
      <div slot="header">
        <span>编辑公告</span>
      </div>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-form-item label="标题" prop="title">
          <el-input v-model="form.title"></el-input>
        </el-form-item>
        <el-form-item label="置顶" prop="is_sticky">
          <el-radio v-model="form.is_sticky" :label="1">是</el-radio>
          <el-radio v-model="form.is_sticky" :label="0">否</el-radio>
        </el-form-item>
        <el-form-item label="内容" prop="content">
          <!-- 富文本编辑器 -->
          <wang-editor v-model="form.content"></wang-editor>
        </el-form-item>
        <el-form-item>
          <el-button @click="handleEdit" type="primary">保存修改</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { Notice } from '@/api/';
import WangEditor from "@/components/WangEditor";

export default {
  name: "Edit",
  props: ['notice_id'],
  components: {
    WangEditor,
  },
  data() {
    return {
      form: {
        notice_id: '',
        title: '',
        is_sticky: '1',
        content: '',
      },
      rules: {
        title: [
          { type: "string", required: true, message: '请输入公告标题', trigger: 'blur' },
          { min: 2, max: 40, message: '公告标题长度在 2 到 40 个字符', trigger: 'blur' }
        ],
        is_sticky: [
          { type: "number", required: true, message: '请选择是否置顶', trigger: 'change' },
        ],
        content: [
          { type: "string", required: true, message: '请填写公告内容', trigger: 'blur' },
        ]
      }
    }
  },
  watch: {
    "$route": 'mounted',
  },
  created() {
    document.title = "编辑公告";
  },
  async mounted() {
    // 获取公告详情
    let { status, data } = await Notice.detail({ notice_id: this.notice_id });
    if (status) {
      // 重新设置编辑器内容
      this.form = data;
    }
  },
  methods: {
    // 保存修改
    handleEdit() {
      this.$refs.form.validate(async (valid) => {
        if (valid) {
          let { status, msg } = await Notice.edit(this.form);
          if (status) {
            this.$message.success(msg);
            // 跳转页面
            this.$router.replace('/notice/list');
          } else {
            this.$message.error(msg);
          }
        }
      });
    }
  }
}
</script>

